/* Google font */
/* @import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@300;400;500;600&display=swap'); */
@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@300;400;500;600&family=Poppins:wght@300;400;500;600&display=swap');



* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}


/* CSS VARIABLES variable declared with -- */

:root {

    /* colors */
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255 , 255, 0.6);
    
    /* media queries */
    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
    
    /* animation transition */
    --transition: all 900ms ease;

}

/* enable this behaviour on chrome via chrome flags */
html {
    scroll-behavior: smooth;
}

/* no scrollbar */
::-webkit-scrollbar {
    display: none;
}

body {
    /* font-family: 'Montserrat Alternates', sans-serif; */
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/assets/bg-texture.png)

}


/* ----------------- GENERAL  STYLES ------------------*/

.container {
    width: var(--container-width-lg);
    margin: 0 auto
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

/* section in each component */
section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary)
}

.text-light {
    color: var(--color-light)
}

a {
    color: var(--color-primary);
    transition: var(--transition)
}

a:hover {
    color: var(--coor-white)
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}


/* ------------------ MEDIA QUERIES ---------------- */

@media screen and (min-width: 1024px) {
    section {
        margin-top: 4rem;
    }
}


/* md device */
@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }
    
    section {
        margin-top: 5rem;
    }
}

/* small devices */
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
        
    }
    
    section > h2 {
        margin-bottom: 2rem;
    }
}